import Api from '../../api/wo';
export default {
	onLoad: function(option) {
		Api.getWOInfo(option.orderId).then(res => {
			console.log(res)
			this.orderId = option.orderId
			this.person = res.data.person
			this.phone = res.data.phone
			this.br = res.data.br
			this.fault = res.data.fault
			this.machine = res.data.machine
			this.sts = res.data.wosts
			const tmp = res.data.images.split(';')
			if (tmp[0] !== '') {
				const urls = []
				for (const index in tmp) {
					const url = tmp[index]
					urls.push(url)
				}
				this.images = urls;
			}
		})
	},
	data() {
		return {
			orderId: '',
			person: '',
			phone: '',
			fault: '',
			machine: '',
			br: '',
			sts: '',
			operationInfo: '',
			images: [],
		};
	},
	methods: {
		setOperationInfo(e) {
			this.operationInfo = e.detail.value
		},
		onImageTouch(index) {
			uni.previewImage({
				current: index,
				urls: this.images
			})
		},
		upOperation() {
			let data = {
				orderId: this.orderId,
				person: this.person,
				operationInfo: this.operationInfo,
			}
			if (data.operationInfo == '') {
				uni.showToast({
					title: '请输入理由',
					icon: 'none'
				})
				return
			}
			uni.showLoading({
				title: '提交中',
			})
			Api.uploadOperation(data).then(res => {
				uni.hideLoading()
				if (res.code == 200) {
					uni.showModal({
						title: "提交成功",
						content: "你已完成提交！",
						showCancel: false,
						confirmText: "完成",
						success: function() {
							uni.hideLoading()
							uni.reLaunch({
								url: '/pages/engineer/engineer'
							})
						}
					})
				} else {
					wx.showToast({
						icon: 'none',
						title: '提交失败',
						mask: true,
						duration: 2000
					})
				}
			})
		},
		acceptNowWO(orderId) {
			uni.showLoading({
				title: '接受中',
			})
			Api.acceptWO(orderId).then(res => {
				uni.hideLoading()
				uni.showToast({
					icon: 'success',
					mask: true,
					title: '接受工单成功',
					duration: 1000
				})
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/engineer/engineer'
					})
				}, 1000)
			})
		},
		goWODetail(orderId) {
			uni.navigateTo({
				url: '/pages/operation_his/operation_his?orderId=' + orderId
			})
		},
		intoChangePerson(orderId) {
			uni.navigateTo({
				url: '/pages/query_nextperson/query_nextperson?orderId=' + orderId
			})
		},
	}
}
